<template>
    <div id="app" data-app>
        <div class="app-shell">
            <app-header
                class="app-shell-header"
                @click-menu="setShowSideBar(true)"
                @click-back="handleClickHeaderBack">
            </app-header>
            <app-sidebar @hide-sidebar="setShowSideBar(false)" @show-sidebar="setShowSideBar(true)"></app-sidebar>
            <div class="app-view-wrapper" ref="appViewWrapper"
                :class="{
                    'app-view-with-header': show
                }">
                <transition
                    @before-enter="handleBeforeEnter"
                    @after-enter="handleAfterEnter"
                    @after-leave="handleAfterLeave">
                    <keep-alive>
                        <router-view
                            v-if="!$route.meta.notKeepAlive"
                            class="app-view"
                            ref="appView"
                            :key="$route.fullPath"
                            :class="{
                                'overflow-scrolling-touch': true
                            }"></router-view>
                    </keep-alive>
                </transition>
                <transition
                    @before-enter="handleBeforeEnter"
                    @after-enter="handleAfterEnter"
                    @after-leave="handleAfterLeave">
                    <router-view
                        v-if="$route.meta.notKeepAlive"
                        class="app-view"
                        ref="appView"
                        :key="$route.fullPath"
                        :class="{
                            'overflow-scrolling-touch': true
                        }"></router-view>
                </transition>
            </div>
            <app-bottom-navigator />
        </div>

        <v-snackbar
            v-model="showToast"
            color="info"
            :timeout="2000"
            >
            {{toastMsg}}
        </v-snackbar>

         <v-dialog
            v-model="showDialog"
            max-width="290"
            >
            <v-card style="background-color:white">
                <v-card-title class="headline">密码修改</v-card-title>

                <v-text-field label="原密码" class="passinput" v-model="password.old"></v-text-field>
                <v-text-field label="新密码" class="passinput" v-model="password.new"></v-text-field>
                <v-text-field label="确认密码" class="passinput" v-model="password.confirm"></v-text-field>

                <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn
                    color="green darken-1"
                    flat="flat"
                    @click="showDialog = false"
                >
                    取消
                </v-btn>

                <v-btn
                    color="blue lighten-1"
                    flat="flat"
                    @click="changePassword"
                >
                    确定
                </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>

import {mapState} from 'vuex';
import AppHeader from '@/components/AppHeader';
import AppSidebar from '@/components/AppSidebar';
import AppBottomNavigator from '@/components/AppBottomNavigator';
import EventBus from '@/event-bus';
import localjson from '@/util/localjson';
import util from '@/util/util';

export default {
    name: 'app',
    components: {
        AppHeader,
        AppSidebar,
        AppBottomNavigator,
    },
    data () {
        return {
            showToast: false,
            toastMsg: '',
            showDialog: false,
            password: {
                old: '',
                new: '',
                confirm: ''
            }
        }
    },
    computed: {
        ...mapState(['show'])
    },
    methods: {
        handleBeforeEnter(el) {
            this.$store.commit('setPageSwitching', true);
            EventBus.$emit('app-page:before-enter');
        },
        handleAfterEnter(el) {
            // 动画结束，恢复 body 上的滚动距离
            el.classList.remove('enable-scroll');
            this.$store.commit('setPageSwitching', false);
        },
        handleAfterLeave(el) {
            el.classList.remove('enable-scroll');
            this.$store.commit('setPageSwitching', false);
            EventBus.$emit('app-page:after-leave');
        },
        handleClickHeaderBack() {
            this.$router.go(-1);
        },
        setShowSideBar(val) {
            this.$store.commit('setShowSideBar', val);
        },
        async changePassword() {
            console.log(123);
            const succ = await util.changePassword(this, this.password);
            if(succ === true) {
                localjson.changePassword(this.password.confirm);
                this.showDialog = false;
                EventBus.$emit('toast:show', '修改密码成功');
            }
        }
    },
    mounted() {
        let $appView = this.$el.querySelector('.app-view-wrapper');
        let touchStartPosX;
        let touchMoveX;
        let touchStartPosY;
        let touchMoveY;
        let minOffset = 60;

        let touchstart = e => {
            touchStartPosX = e.touches[0].pageX;
            touchStartPosY = e.touches[0].pageY;
        };

        let touchmove = e => {
            touchMoveX = e.touches[0].pageX - touchStartPosX;
            touchMoveY = e.touches[0].pageY - touchStartPosY;
        };

        let touchend = e => {
            // 首页不能继续后退
            if (touchMoveX > minOffset
                && Math.abs(touchMoveY) < minOffset
            ) {
                if(this.$router.currentRoute.path.indexOf('/', 1) > 1){
                    this.$router.go(-1);
                }
            }
            touchMoveX = 0;
            touchMoveY = 0;

        };

        $appView.addEventListener('touchstart', touchstart);
        $appView.addEventListener('touchmove', touchmove);
        $appView.addEventListener('touchend', touchend);

        EventBus.$on('toast:show', msg => {
            this.showToast = true;
            this.toastMsg = msg;
        })

        EventBus.$on('password:show', () => {
            this.showDialog = true;
            this.password = {
                old: '',
                new: '',
                confirm: ''
            };
        })
    }
};
</script>

<style lang="stylus">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


#app
    font-family 'Avenir', Helvetica, Arial, sans-serif
    -webkit-font-smoothing antialiased
    -moz-osx-font-smoothing grayscale
    color #2c3e50
    height: 100%

</style>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.passinput
    margin 5px 20px

.app-shell
    position absolute
    top 0
    bottom 0
    left 0
    right 0
    height 100%
    width 100%



    .app-shell-header
        position fixed
        top 0
        left 0
        right 0
        z-index: 100

    .app-view-wrapper
        position absolute
        top 0
        left 0
        right 0
        bottom 0

        &.app-view-with-header
            top $app-header-height

        &.app-view-with-footer
            bottom $app-footer-height

        .app-view
            position absolute
            top 0
            left 0
            right 0
            transition transform 0.4s cubic-bezier(.55, 0, .1, 1)
            background: $material-theme.bg-color
            color: $material-theme.text-color

            &.overflow-scrolling-touch
                -webkit-overflow-scrolling touch

            // 隐藏掉scrollbar
            &::-webkit-scrollbar
                width 0px
                background transparent

            &.slide-left-enter
                transform translate(100%, 0)

            &.slide-right-enter
                transform translate(-100%, 0)

            &.slide-right-leave-active
                transform translate(100%, 0)

            &.slide-left-leave-active
                transform translate(-100%, 0)

            &.slide-left-enter-active,
            &.slide-left-leave-active,
            &.slide-right-enter-active,
            &.slide-right-leave-active
                overflow-y auto
                bottom 0

            &.enable-scroll
                overflow-y auto
                bottom 0
</style>



