import low from 'lowdb'
import LocalStorage from 'lowdb/adapters/LocalStorage'

const adapter = new LocalStorage('db')
const db = low(adapter)
db.defaults({ user: {}, currentPage: { text: '公告通知', path:'/notice' } }).write();

export default {

    changePassword(pass) {
        return db.set('user.password', pass).write();
    },

    getUser() {
        return db.get('user').value();
    },

    setUser(u) {
        db.set('user', u).write();
    },

    getCurrentPage() {
        return db.get('currentPage').value();
    },

    setCurrentPage(page) {
        db.set('currentPage', page).write();
    },

    clearDb() {
        db.set('user', {}).write();
        db.set('currentPage', {}).write();
    },
};



