import axios from 'axios';
import localjson from './localjson';
import EventBus from '@/event-bus';

let util = {
    token: localjson.getUser().token
};

util.ajax = axios.create({
    baseURL: "http://localhost:6634/",
    timeout: 3000,
    headers: {
        "content-type": "application/json"
    }
});

util.get = function(vm, url) {
    util.ajax.defaults.headers.authorization = "Bearer " + util.token;
    return util.ajax.get(url).then(res => {
        return res.data.result;
    }).catch(err => {
        if(err.response.status == 401){
            vm.$store.appSidebar.dispatch('accountLogout');
            vm.$router.push({name: 'login'});
        } else {
            EventBus.$emit('toast:show', err.response.data.error.message);
            throw err.response.data;
        }
    });
}

util.post = function(vm, url, params) {
    util.ajax.defaults.headers.authorization = "Bearer " + util.token;
    return util.ajax.post(url, params).then(res => {
        return res.data.result;
    }).catch(err => {
        if(err.response.status == 401){
            vm.$store.appSidebar.dispatch('accountLogout');
            vm.$router.push({name: 'login'});
        } else {
            EventBus.$emit('toast:show', err.response.data.error.message);
            throw err.response.data;
        }
    });
}

util.login = (vm, username, password) => {
    return {username: username, password: password, name: 'test', token: 'adfsafdsafda'};
    // return util.post(vm, 'api/Account/login', {
    //     usernameOrEmailAddress: username,
    //     password: password
    // });
}


util.changePassword = (vm, password) => {
    return true;
    // return util.post(vm, 'api/Account/login', {
    //     usernameOrEmailAddress: username,
    //     password: password
    // });
}
export default util;


