<template>
    <sidebar v-model="sidebarStatus" :enableSwipeOut="enableSwipeOut">
        <!-- sidebar 内容部分 -->
        <div
            class="app-sidebar-content">
            <!-- 头部 -->
            <div class="app-sidebar-header">
                <div class="user-avatar">
                    <v-icon light class="user-avatar-icon">face</v-icon>
                 </div>
                 <div v-if="user && user.name" class="user-info">
                     <div class="user-name"><v-icon>person</v-icon>{{user.name}}</div>
                     <div class="user-bianhao"><v-icon>code</v-icon>{{user.username}}</div>
                 </div>
                 <p v-else class="user-login">登录</p>
            </div>

            <!-- 导航列表分区块 -->
            <div v-if="blocks" class="app-sidebar-blocks">
                <ul>
                    <!-- 单个区块 -->
                    <li v-for="(block, index) in blocks" :key="index" class="app-sidebar-block">
                        <div v-if="block.sublistTitle" class="sub-list-title">{{ block.sublistTitle }}</div>
                        <ul v-if="block.list">
                            <li v-for="item in block.list" :key="item.text" 
                                :class="currPage.text == item.text ? 'li-active' : ''"
                                @click.stop="itemClicked(item)" v-ripple="{class: 'grey--text'}">
                                <span v-if="item.icon || item.image || item.svg " class="app-sidebar-block-left-icon">
                                    <img v-if="item.image" :src="item.image" :alt="item.alt"></img>
                                    <icon v-else-if="item.svg" :name="item.svg"></icon>
                                    <v-icon v-else-if="item.icon" class="grey--text text--lighten-1">{{ item.icon }}</v-icon>
                                </span>
                                <span v-if="item.text" class="app-sidebar-block-text">{{ item.text }}</span>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
    </sidebar>
</template>

<script>
import {mapState, mapActions} from 'vuex';
import Sidebar from './Sidebar';
import localjson from '../util/localjson';
import EventBus from '@/event-bus';

export default {
    data() {
        return {
            blocks: [
                {
                    list: [
                        {
                            text: '通讯录',
                            icon: 'people',
                            route: '/contact'
                        },
                        {
                            text: '个人事务',
                            icon: 'person',
                            route: '/personal'
                        },
                        {
                            text: '公告通知',
                            icon: 'info',
                            route: '/notice'
                        },
                        {
                            text: '引航信息',
                            icon: 'list',
                            route: '/pilot'
                        },
                        {
                            text: '其他资料',
                            icon: 'grid_on',
                            route: '/others'
                        }
                    ]
                },
                {
                    list: [
                        {
                            text: '修改密码',
                            icon: 'vpn_key',
                            action: 'changepassword'
                        },
                        {
                            text: '退出账户',
                            icon: 'exit_to_app',
                            action: 'logout'
                        }
                    ]
                }
            ]
        };
    },
    components: {
        Sidebar,
    },
    computed: {
        ...mapState([
            'user', 
            'currentPage',
            'showSideBar',
            'enableSwipeOut'
        ]),
        sidebarStatus: {
            get() {
                return this.showSideBar;
            },
            set(val) {
                if (val) {
                    this.$emit('show-sidebar');
                }
                else {
                    this.$emit('hide-sidebar');
                }
            }
        },
        currPage: {
            get() {
                return this.currentPage;
            },
            set(val) {
                this.$store.commit('setCurrentPage', val);
            }
        }
    },
    mounted () {
        this.$store.commit('reloadDataFromDb');
    },
    activated() {
        this.password = '';
        this.username = '';
        this.$store.commit('setAppHeader', {
            show: false,
        });
        this.$store.commit('setEnableSwipeOut', false);
    },
    methods: {
        close() {
            this.sidebarStatus = false;
        },
        itemClicked(item) {
            if (item.action) {
                this[item.action] && this[item.action]();
                return;
            }
            if (!item.route) {
                return;
            }

            this.$router.push(item.route);
            this.close();

        },
        logout() {
            this.$store.commit('accountLogout');
            this.$router.replace("/");
        },
        changepassword() {
            
            this.$store.commit('setShowSideBar', false);
            EventBus.$emit('password:show');
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

// 左侧触发滑动宽度
$swipe-width = 20px

ul,li
    padding 0
    margin 0
    list-style none
a
    text-decoration none
    color #333

p
    margin 0

.app-sidebar-header
    background: $theme.primary
    padding: 10px
    text-align: center
    color: #fff
    display: flex
    height: 112px

    .user-avatar
        display: flex
        justify-content center
        align-items center
        margin-right: 7px

        i
            font-size: 50px
            height: 100%

    .user-login
        line-height: 92px
        font-size: 18px
        text-align: left

    .user-info
        text-align: left

        >div
            margin 15px 0

            i
                font-size 22px
                margin-right 5px


.app-sidebar-content
    &.app-sidebar-content-right
        box-shadow -3px 0 8px 1px rgba(0, 0, 0, 0.4)

        &.app-sidebar-title,
        &.app-sidebar-blocks
            text-align right

    .app-sidebar-title-left-icon,
    .app-sidebar-block-left-icon
        display inline-block
        width ($app-sidebar-left-icon-size + 10)px
        height 100%

        img
            vertical-align middle
            width ($app-sidebar-left-icon-size)px
            height ($app-sidebar-left-icon-size)px
        svg
            position relative
            left 0
            top 0
            transform none
            vertical-align middle
            height ($app-sidebar-left-icon-size)px
            width ($app-sidebar-left-icon-size)px

        .material-icons
            font-size ($app-sidebar-left-icon-size)px

    .app-sidebar-block-text
        display inline-block
        height 100%
        vertical-align middle

    .app-sidebar-title-right-logo,
    .app-sidebar-block-right-logo
        float right

        img
            width 20px
            height 20px
            margin-right 10px


    .app-sidebar-title
        color #fff
        padding 0 10px
        font-size 16px
        font-weight bold
        height $app-sidebar-title-height
        line-height $app-sidebar-title-height
        background: $theme.primary
        text-align left

    .app-sidebar-user
        padding 0 10px
        font-size 16px
        .user-avatar
            margin 30px auto 0 auto
            height 80px
            width 80px

            i
                font-size 80px
                height: 100%
                color #666
        .user-info
            padding 10px 0 20px
            text-align center
            border-bottom 1px solid #e0e0e0
            >div
                margin 15px 0
                i
                    font-size 22px
                    margin-right 5px

    .app-sidebar-blocks
        text-align left

        .app-sidebar-block
            padding 10px 0
            border-bottom 1px solid #e0e0e0
            color #333
            position: relative

            .sub-list-title
                height $app-sidebar-nav-height
                line-height $app-sidebar-nav-height
                text-indent ($app-sidebar-left-icon-size)px
                font-weight bold
                color #888

            li
                padding-left 15px
                height $app-sidebar-nav-height
                line-height $app-sidebar-nav-height
                position relative

                

                &:last-child
                    border none

            .li-active
                background #F5F5F5

            &:last-child
                border-bottom none

.app-sidebar-toast
    position: absolute
    bottom: 20px
    left: 50%
    transform: translateX(-50%)
    border-radius: 4px
    padding: 10px 30px
    background:rgba(0,0,0,0.52)
    color: #fff
    font-size: 14px

.toast-leave-active, .toast-enter-active
  transition: all .5s ease

.toast-enter, .toast-leave-to, .toast-leave-active
  transform: translateY(10px) translateX(-50%);
  opacity: 0;

</style>



