import Vue from 'vue';
import Vuex from 'vuex';
import appShell from './modules/app-shell';
import localjson from '../util/localjson';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        user: {},
        currentPage: {},
        
        show: false,   //是否展示顶部导航栏
        
        title: '舟山引航',  // 标题内容
        
        showMenu: false,  //是否展示左侧菜单图标

        showBack: false, //是否展示左侧收藏夹图标

        actions: [], //右侧操作按钮数组

        enableSwipeOut: false, //

        isPageSwitching: false,

        showSideBar: false,

        showBottom: true, 
    },
    mutations: {
        setAppHeader(state, appHeader){
            state.appHeader = Object.assign(state, appHeader);
        },
        setEnableSwipeOut(state, val){
            state.enableSwipeOut = val;
        },
        setPageSwitching(state, val){
            state.isPageSwitching = val;
        },
        setShowSideBar(state, val){
            state.showSideBar = val;
        },
        setUser(state, usr){
            state.user = usr;
        },
        setCurrentPage(state, page){
            state.currentPage = page;
        },
        accountLogout(state) {
            state.user = {};
            localjson.clearDb();
        },
        reloadDataFromDb(state){
            state.currentPage = localjson.getCurrentPage();
            state.user = localjson.getUser();
        }
    },
    modules: {
        appShell,
    }
});



