<template>
    <transition
        name="slide-up">
        <footer class="app-bottom-navigator-wrapper" v-show="showBottom && navs.length">
            <slot name="navs">
                <v-bottom-nav
                    :value="showBottom"
                    :active.sync="currentPage.route"
                    absolute
                    color="transparent">
                    <v-btn
                        v-for="(nav, index) in navs"
                        :key="'key' + index"
                        color="blue lighten-1"
                        :value="nav.route"
                        @click="handleNavClick(nav.route)"
                        flat>
                        <span>{{ nav.text }}</span>
                        <v-icon>{{ nav.icon }}</v-icon>
                    </v-btn>
                </v-bottom-nav>
            </slot>
        </footer>
    </transition>
</template>

<script>
import {mapState} from 'vuex';
import EventBus from '@/event-bus';

export default {
    name: 'app-bottom-navigator',
    data(){
        return {
            navs: [
                {
                    text: '通讯录',
                    icon: 'people',
                    route: '/contact'
                },
                {
                    text: '个人事务',
                    icon: 'person',
                    route: '/personal'
                },
                {
                    text: '公告通知',
                    icon: 'info',
                    route: '/notice'
                },
                {
                    text: '引航信息',
                    icon: 'list',
                    route: '/pilot'
                },
                {
                    text: '其他资料',
                    icon: 'grid_on',
                    route: '/others'
                }
            ]
        }
    },
    computed: {
        ...mapState([
            'showBottom',
            'currentPage',
        ])
    },
    methods: {

        handleNavClick(route) {
            console.log(route);
            if (route) {
                this.$router.replace(route);
            }
        }

    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.app-bottom-navigator-wrapper
    height $app-footer-height
    transition transform 0.3s ease-out

    &.slide-up-enter,
    &.slide-up-leave-to
        transform translate(0, 100%)

</style>



