/**
 * @file router
 * @author huanghuiquan(huanghuiquanhhh@gmail.com)
 */

import Vue from 'vue';
import Router from 'vue-router';
import * as types from './store/mutation-types';
import localjson from '@/util/localjson';

// 定义切割点，异步加载路由组件
let Contact = () => import('@/pages/contact/Contact.vue');

let Personal = () => import('@/pages/personal/Personal.vue');
let PersonalQjkq = () => import('@/pages/personal/Qjkq.vue');
let PersonalKqjf = () => import('@/pages/personal/Kqjf.vue');
let PersonalGztd = () => import('@/pages/personal/Gztd.vue');
let PersonalPxjy = () => import('@/pages/personal/Pxjy.vue');
let PersonalWylj = () => import('@/pages/personal/Wylj.vue');
let PersonalBjgj = () => import('@/pages/personal/Bjgj.vue');

let Notice = () => import('@/pages/notice/Notice.vue');
let NoticeGgtz = () => import('@/pages/notice/Ggtz.vue');
let NoticeXwdt = () => import('@/pages/notice/Xwdt.vue');
let NoticeHytz = () => import('@/pages/notice/Hytz.vue');
let NoticeTgjg = () => import('@/pages/notice/Tgjg.vue');
let NoticeAqtx = () => import('@/pages/notice/Aqtx.vue');
let NoticeGzxx = () => import('@/pages/notice/Gzxx.vue');
let NoticeDdtz = () => import('@/pages/notice/Ddtz.vue');
let NoticeHqqk = () => import('@/pages/notice/Hqqk.vue');
let NoticeQttz = () => import('@/pages/notice/Qttz.vue');

let Pilot = () => import('@/pages/pilot/Pilot.vue');
let PilotYhjh = () => import('@/pages/pilot/Yhjh.vue');
let PilotGrjh = () => import('@/pages/pilot/Grjh.vue');
let PilotScsj = () => import('@/pages/pilot/Scsj.vue');
let PilotJfhd = () => import('@/pages/pilot/Jfhd.vue');
let PilotZtxx = () => import('@/pages/pilot/Ztxx.vue');
let PilotZqdd = () => import('@/pages/pilot/Zqdd.vue');
let PilotTsjh = () => import('@/pages/pilot/Tsjh.vue');
let PilotGzgf = () => import('@/pages/pilot/Gzgf.vue');
let PilotQtxx = () => import('@/pages/pilot/Qtxx.vue');

let Others = () => import('@/pages/others/Others.vue');
let OthersMtbw = () => import('@/pages/others/Mtbw.vue');
let OthersTlzl = () => import('@/pages/others/Tlzl.vue');
let OthersCxsj = () => import('@/pages/others/Cxsj.vue');
let OthersHqzl = () => import('@/pages/others/Hqzl.vue');
let OthersJlhd = () => import('@/pages/others/Jlhd.vue');
let OthersQtzl = () => import('@/pages/others/Qtzl.vue');

let NotFound = () => import('@/pages/NotFound.vue');
let Login = () => import('@/pages/Login.vue');

Vue.use(Router);

export function createRouter() {
    let router = new Router({

        // history 模式，需要服务器后端配合做路由代理，将所有的前端路由同步代理到 /
        mode: 'hash',
        /* eslint-disable no-undef */
        base: __webpack_public_path__,
        /* eslint-enable no-undef */
        routes: [
            {
                path: '/',
                name: 'login',
                component: Login
            },
            {
                path: '/contact',
                name: 'contact',
                component: Contact
            },
            {
                path: '/personal',
                name: 'personal',
                component: Personal
            },
            {   path: '/personal/qjkq', component: PersonalQjkq   },
            {   path: '/personal/kqjf', component: PersonalKqjf   },
            {   path: '/personal/gztd', component: PersonalGztd   },
            {   path: '/personal/pxjy', component: PersonalPxjy   },
            {   path: '/personal/wylj', component: PersonalWylj   },
            {   path: '/personal/bjgj', component: PersonalBjgj   },
            {
                path: '/notice',
                name: 'notice',
                component: Notice
            },
            {   path: '/notice/ggtz', component: NoticeGgtz   },
            {   path: '/notice/xwdt', component: NoticeXwdt   },
            {   path: '/notice/hytz', component: NoticeHytz   },
            {   path: '/notice/tgjg', component: NoticeTgjg   },
            {   path: '/notice/aqtx', component: NoticeAqtx   },
            {   path: '/notice/gzxx', component: NoticeGzxx   },
            {   path: '/notice/ddtz', component: NoticeDdtz   },
            {   path: '/notice/hqqk', component: NoticeHqqk   },
            {   path: '/notice/qttz', component: NoticeQttz   },
            {
                path: '/pilot',
                name: 'pilot',
                component: Pilot
            },
            {   path: '/pilot/yhjh', component: PilotYhjh   },
            {   path: '/pilot/grjh', component: PilotGrjh   },
            {   path: '/pilot/scsj', component: PilotScsj   },
            {   path: '/pilot/jfhd', component: PilotJfhd   },
            {   path: '/pilot/ztxx', component: PilotZtxx   },
            {   path: '/pilot/zqdd', component: PilotZqdd   },
            {   path: '/pilot/tsjh', component: PilotTsjh   },
            {   path: '/pilot/gzgf', component: PilotGzgf   },
            {   path: '/pilot/qtxx', component: PilotQtxx   },
            {
                path: '/others',
                name: 'others',
                component: Others
            },
            {   path: '/others/mtbw', component: OthersMtbw   },
            {   path: '/others/tlzl', component: OthersTlzl   },
            {   path: '/others/cxsj', component: OthersCxsj   },
            {   path: '/others/hqzl', component: OthersHqzl   },
            {   path: '/others/jlhd', component: OthersJlhd   },
            {   path: '/others/qtzl', component: OthersQtzl   },
            {
                path: '*',
                name: 'notFound',
                component: NotFound
            }
        ]
    });

    /**
     * 切换动画名称
     *
     * @type {string}
     * @const
     */
    const SLIDE_LEFT = 'slide-left';

    /**
     * 切换动画名称
     *
     * @type {string}
     * @const
     */
    const SLIDE_RIGHT = 'slide-right';

    router.beforeEach((to, from, next) => {
        const usr = localjson.getUser();
        if(to.path !== "/"){
            if(!usr || !usr.token) {
                next({path: '/'});
            }
            else {
                next();
            }
        }
        else {
            if(usr && usr.token) {
                const pag = localjson.getCurrentPage();
                if(pag && pag.path) {
                    next({path: pag.path});
                } else {
                    next({path: 'notice'});
                }
            }
            else {
                next();
            }
        }
    });

    return router;
}




